import React, {useEffect, useState} from "react";
import classes from "./style.module.scss";

import { calculate } from '../../calculator';


export const App = () => {
  const [input, setInput] = useState("");

  useEffect(() => {

  }, [])

  const buttonsHandler = (e: React.MouseEvent) => {
    window.navigator.vibrate(40);
    const target = e.target as HTMLButtonElement;
    if (target.dataset.value === "AC") {
      setInput("");
    } else {
      setInput(last => last + target.dataset.value);
    }
  }

  enum ButtonType {
    system,
    op,
    num,
    submit
  }

  interface IButton {
    type: ButtonType,
    value: string,
    label: string
  }

  const opButtons: IButton[][] = [
    [
      { type: ButtonType.op, value: "", label: "" },
      { type: ButtonType.op, value: "", label: "" },
      { type: ButtonType.op, value: "", label: "x^2" },
      { type: ButtonType.op, value: "", label: "x^n" },
      { type: ButtonType.op, value: "", label: "logn" },
      { type: ButtonType.op, value: "", label: "ln" },
    ],
    [
      { type: ButtonType.op, value: "", label: "( – )" },
      { type: ButtonType.op, value: "", label: "" },
      { type: ButtonType.op, value: "", label: "x(-1)" },
      { type: ButtonType.op, value: "", label: "sin" },
      { type: ButtonType.op, value: "", label: "cos" },
      { type: ButtonType.op, value: "", label: "tan" },
    ],
    [
      { type: ButtonType.op, value: "", label: "STO" },
      { type: ButtonType.op, value: "", label: "ENG" },
      { type: ButtonType.op, value: "", label: "(" },
      { type: ButtonType.op, value: "", label: ")" },
      { type: ButtonType.op, value: "", label: "S<>D" },
      { type: ButtonType.op, value: "", label: "M+" },
    ],
  ]

  const numButtons: IButton[][] = [
    [
      { type: ButtonType.num, value: "7", label: "7" },
      { type: ButtonType.num, value: "8", label: "8" },
      { type: ButtonType.num, value: "9", label: "9" },
      { type: ButtonType.system, value: "DEL", label: "DEL" },
      { type: ButtonType.system, value: "AC", label: "AC" },
    ],
    [
      { type: ButtonType.num, value: "4", label: "4" },
      { type: ButtonType.num, value: "5", label: "5" },
      { type: ButtonType.num, value: "6", label: "6" },
      { type: ButtonType.op, value: "*", label: "×" },
      { type: ButtonType.op, value: "/", label: "÷" },
    ],
    [
      { type: ButtonType.num, value: "1", label: "1" },
      { type: ButtonType.num, value: "2", label: "2" },
      { type: ButtonType.num, value: "3", label: "3" },
      { type: ButtonType.op, value: "+", label: "+" },
      { type: ButtonType.op, value: "-", label: "–" },
    ],
    [
      { type: ButtonType.num, value: "0", label: "0" },
      { type: ButtonType.num, value: ".", label: "." },
      { type: ButtonType.op, value: "", label: "" },
      { type: ButtonType.op, value: "", label: "" },
      { type: ButtonType.op, value: "=", label: "=" },
    ],
  ]

  return (
    <div className={classes.wrapper}>
      <div className={classes.root}>
        <input type="text" className={classes.display} value={input} placeholder="0" disabled />
        <div className={classes.opBlock}>
          { opButtons.map(row =>
            <div className={classes.row}>
              { row.map(cell =>
                <button
                  data-type={ ButtonType[cell.type] }
                  data-value={ cell.value }
                  className={classes.cell}
                >{ cell.label }</button>)
              }
            </div>
          )}
        </div>
        <div className={classes.numBlock} onClick={buttonsHandler}>
          { numButtons.map(row =>
            <div className={classes.row}>
              { row.map(cell =>
                <button
                  data-type={ ButtonType[cell.type] }
                  data-value={ cell.value }
                  className={classes.cell}
                >{ cell.label }</button>)
              }
            </div>
          )}
        </div>
      </div>
    </div>
  )
}